.nav{
    width: 100%;
    height: 30px;
}
.search{
    margin-left: 50px;
    text-align: center;
    width: 300px;
    height: 20px;
    border: none;
    border-radius: 5px;
    background-color:rgb(128, 128, 128);
    position: absolute;
    top: 5px;
    line-height: 20px;
}
.search i{
    position: absolute;
    top: 1px;
    left: 100px;
    color: #fff;
}
.search p{
    position: absolute;
    top: -13px;
    left: 120px;
    color: #fff;
}
.taobao{
    width: 20px;
    height: 20px;
    position: absolute;
    top: 10px;
    left: 20px;
}
.lunbotu{
    height: 140px;
    width: 100%;
}
.like{
    width: 300px;
    height: 20px;
    margin-left: 50px;
    font-size: 30px;
}
.con{
    margin-left: 20px;
    margin-top: 20px;
}
.a {
    width: 150px;
    height: 220px;
    background-color: #fff;
    float: left;
    margin: 10px;
    border:2px solid rgb(128, 128, 128);
}
.pic {
    width: 110px;
    padding-top: 15px;
    padding-left: 20px;
}
p {
    text-align: center;
}